body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body, html {
    height: 100%;
    width: 100%;
    color: #a09fa3;
    background: rgb(233, 231, 231);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
    color: #433261;
    font-size: 20px;
}

h2 {
    color: #4c4c4c;
    font-size: 16px;
}

h3 {
    color: #4b4a4c;
    font-size: 14px;
}

li {
    list-style-type: none;
}
.header {
    height: 40px;
    background: #7c60e3;
    color: white;
    text-align: center;
    line-height: 2.3;
    font-weight: bold;
}
.selectUsers {
    width: 100%;
}
.listHeader {
    width:100%;
}

.companyListItem {
    width: 100%;
}

.smallIcon {
    font-size: 18px;
    margin-top: 20px;
    margin-left: 8px;
}

.userCount {
    font-size: 14px;
    display: block;
    text-align: center;
}

.deleteCompany {
    margin-left: 10px!important;
    margin-top: 10px!important;
    height: 40px;
}
.companyList {
    margin-top: 30px;
    text-align: center;
}

.list {
    margin-bottom: 25px;
}

.companyNameInput {
    margin-bottom: 5px;
    width: 95%;
}

.companyDescInput {
    width: 95%;
}

.cancelEntry {
    margin-left: 20px!important;
}

.buttonContainer {
    margin-bottom: 20px!important;
}

